body{
  background: #F4F4F4;
}

.content {
  padding-top: 3rem;
  display: flex;
}

.main {
  background-color: white;
  flex: 66%;
  margin: 0 1rem;
  border: solid 1px rgba(160, 160, 160, 0.3);
}

.footer-container {
  flex: 33%;
}

@media only screen and (max-width: 960px){
  .content { flex-direction: column;}
  .content :nth-child(1) {flex: 1; order: 2; }
  .content :nth-child(2) {flex: 1; order: 1 }
}